<template>
  <div id="order-edit">
    <div class="btn">
      <button @click="jumpIndex()">首页</button>
      <button @click="setMainTitle()">设为标题</button>
      <button @click="addUnderLine()">加下划线</button>
      <button @click="addDate()">添加日期</button>
      <button @click="addTime()">添加时间</button>
      <button @click="saveData(templateName)">保存</button>
      <button @click="saveAs()">另存为</button>
    </div>
    <div class="pick-color">
      <ul>
        <li>日期背景选择：
          <ColorPicker v-model="colors.dateBgColor" alpha recommend/>
        </li>
        <li>日期颜色选择：
          <ColorPicker v-model="colors.dateColor" alpha recommend/>
        </li>
        <li>时间颜色选择：
          <ColorPicker v-model="colors.timeColor" alpha recommend/>
        </li>
        <li>会议颜色选择：
          <ColorPicker v-model="colors.meetColor" alpha recommend/>
        </li>
        <li>内容颜色选择：
          <ColorPicker v-model="colors.contentBgColor" alpha recommend/>
        </li>
        <li>割线颜色选择：
          <ColorPicker v-model="colors.lineColor" alpha recommend/>
        </li>
      </ul>
    </div>
    <div v-for="(val,index) in dates" @click="bigIndex=index">
      <h2 :style="{backgroundColor:colors.dateBgColor}">
        <input type="text" placeholder="输入日期" v-model="val.title" :style="{color:colors.dateColor}">
        <i class="del" @click="delDate(index)"></i>
      </h2>
      <div class="time-wrap" :style="{backgroundColor:colors.contentBgColor}">
        <div class="time-content"
             v-for="(con,key) in val.content"
             @click="timeIndex=key"
             :style="{borderBottomColor:colors.lineColor}"
             :class="[{title:con.isTitle},{underline:con.isUnderLine}]">
          <span>
            <i class="del-time" @click="delTime(index,key)"></i>
            <input type="text" placeholder="输入时间" v-model="con.time" :style="{color:colors.timeColor}">
          </span>
          <p>
            <input type="text" placeholder="输入议程" v-model="con.meeting" :style="{color:colors.meetColor}">
          </p>

        </div>
      </div>
    </div>
    <!-- 另存为 -->
    <Modal
      v-model="isShowSaveAs"
      width="400"
      :closable="false"
      :mask-closable="false"
      @on-ok="saveAsOk"
    >
      <div slot="header" style="color: #2d8cf0;text-align:center">
        <span>请输入模板名称</span>
      </div>
      <div style="text-align: center">
        <Input v-model="saveAsName" placeholder="请输入模板名称..." style="width: 300px"></Input>
      </div>
    </Modal>
  </div>
</template>
<script>
  import Wilddog from 'wilddog'
  import *as common from "@/utils/common.js"
  import {mapGetters} from 'vuex';


  export default {
    name: "OrderEdit",
    props: ['arthas'],
    data() {
      return {
        dates: [
          {
            title: "",
            content: [
              {
                time: "",
                meeting: "",
                isTitle: false,
                isUnderLine: false
              },
            ]
          },
        ],
        colors: {
          dateBgColor: "#008fd3",
          dateColor: "#ffffff",
          timeColor: "#ffffff",
          meetColor: "#ffffff",
          contentBgColor: "#001c3c",
          lineColor: "#008fd3"
        },
        templateName: "",
        bigIndex: 0,
        timeIndex: 0,
        delBigIndex: 0,
        isShowSaveAs: false, // 是否显示另存为对话框
        saveAsName: '',  // 另存为名字
        list: []
      }
    },
    watch: {
      arthas: function (newVal, oldVal) {
        this.dates = newVal.dates;
        this.colors = newVal.colors;
      }
    },
    created() {
      let _this = this;
      this.dates = this.arthas;
      this.templateName = this.$route.params.templateName;


      const config = {
        syncURL: common.syncURL + "/list" //输入节点 URL
      };
      wilddog.initializeApp(config);
      const ref = wilddog.sync().ref();
      ref.on("value", snapshot => {
        if (snapshot.val()) {
          _this.list = snapshot.val();
          ref.off();
        }
      });
    },
    computed: {
      ...mapGetters([
        'getList'
      ])
    },
    methods: {
      /**
       * 跳转到首页
       */
      jumpIndex() {
        this.$router.push("/")
      },
      /**
       * 添加时间块
       */
      addTime() {
        this.dates[this.bigIndex].content.splice(this.timeIndex + 1, 0, {
            times: "",
            meeting: "",
            isTitle: false,
            isUnderLine: false
          }
        )
      },
      /**
       * 添加日期块
       */
      addDate() {
        this.dates.splice(this.bigIndex + 1, 0, {
          title: "",
          content: [
            {
              times: "",
              meeting: "",
              isTitle: false,
              isUnderLine: false
            },
          ]
        })
      },
      /**
       * 删除日期块
       */
      delDate(val) {
        if (this.dates.length == 1) {
          return
        }
        this.dates.splice(val, 1)
      },
      /**
       * 删除时间块
       */
      delTime(val1, val2) {
        if (this.dates[val1].content.length == 1) {
          return
        }
        this.dates[val1].content.splice(val2, 1)
      },
      /**
       * 设置为标题
       */
      setMainTitle() {
        let toggle = this.dates[this.bigIndex].content[this.timeIndex].isTitle;
        this.dates[this.bigIndex].content[this.timeIndex].isTitle = !toggle;
      },
      /**
       * 添加下划线
       */
      addUnderLine() {
        let toggle = this.dates[this.bigIndex].content[this.timeIndex].isUnderLine;
        this.dates[this.bigIndex].content[this.timeIndex].isUnderLine = !toggle;
      },
      /**
       * 野狗云存储数据
       */
      saveData(name, isSaveAs) {
        const config = {
          syncURL: common.syncURL //本地测试版
        };
        wilddog.initializeApp(config);
        const dates = wilddog.sync().ref(`${name}/dates`);
        const colors = wilddog.sync().ref(`${name}/colors`);
        dates.set(this.dates);
        colors.set(this.colors);
        if (isSaveAs) {
          this.$Modal.confirm({
            title: 'Title',
            content: '<p>保存成功!是否跳转到保存的页面？</p>',
            okText: 'OK',
            cancelText: 'Cancel',
            onOk: () => {
              this.$router.push({path: `/home/${name}`})
            }
          });
        } else {
          this.$Modal.success({
            title: '提示',
            content: '保存成功！'
          });
        }
      },
      /**
       * 另存为
       */
      saveAs() {
        this.isShowSaveAs = true;
      },
      saveAsOk() {
        if (this.saveAsName == '') {
          this.$Message.info('模板名称不能为空');
          return;
        } else {
          // 随机生成字符串，用来获取模板数据
          let str = Math.random().toString(36).substr(2);
          let data = {};
          data[str] = this.saveAsName;

          this.list.push(data);

          this.saveData(str, true);
          const config = {
            syncURL: common.syncURL
          };
          wilddog.initializeApp(config);
          const ref = wilddog.sync().ref();
          ref.child("list").set(this.list);
          ref.off();
        }
      }
    }
  }
</script>


<style scoped lang="less">
  #order-edit {
    position: relative;
    .btn {
      position: fixed;
      bottom: 50px;
      left: 0;
      width: 80px;
      z-index: 9;
      button {
        display: block;
        width: 50px;
        border-radius: 10px;
        padding: 10px 10px;
        font-size: 14px;
        margin: 10px auto 0;
        background: #008fd3;
        color: #ff4;
        text-align: center;
      }
    }
    .pick-color {
      width: 100%;
      background: cyan;
      padding: 5px 0;
      ul {
        li {
          float: left;
          width: 32%;
          margin: 5px 3px;
        }
        &:after {
          content: "";
          display: block;
          height: 0;
          clear: both;
          visibility: hidden
        }
      }
    }
    h2 {
      position: relative;
      font-size: 18px;
      background: #008fd3;
      color: #fff;
      padding-left: 20px;
      line-height: 2;
      height: 50px;
      .del {
        position: absolute;
        right: 10px;
        top: 10px;
        display: block;
        width: 30px;
        height: 30px;
        background: url("../assets/delete.png") no-repeat;
        background-size: 30px 30px;
        cursor: pointer;
      }
    }
    .time-wrap {
      background: #001c3c;
      color: #fff;
    }
    .time-content {
      font-size: 14px;
      padding: 10px 20px;
      span {
        position: relative;
        float: left;
        width: 90px;
        .del-time {
          position: absolute;
          top: 0;
          left: -20px;
          display: block;
          width: 20px;
          height: 20px;
          background: url("../assets/close.png") no-repeat;
          background-size: 20px 20px;
          cursor: pointer;
        }
      }

      p {
        float: left;
        width: 370px;
        font-size: 14px;
        position: relative;
        .del-meet {
          position: absolute;
          top: 0;
          left: -20px;
          display: block;
          width: 20px;
          height: 20px;
          background: url("../assets/close.png") no-repeat;
          background-size: 20px 20px;
          cursor: pointer;
        }
      }
      &.title {
        font-weight: bold;
      }
      &.underline {
        padding-top: 10px;
        padding-bottom: 10px;
        border-bottom: 1px dashed #ccc;
      }
      &:after {
        content: "";
        display: block;
        height: 0;
        clear: both;
        visibility: hidden
      }
    }

  }

  input {
    display: block;
    width: 100%;
    height: 100%;
    background: transparent;
    color: #fff;
  }

</style>



