<template>
  <div id="home">
    <div class="edit">
      <order-edit :arthas="{dates,colors}"></order-edit>
    </div>
    <div class="view" id="view">
      <top></top>
      <order-view :arthas="{dates,colors}"></order-view>
      <under></under>
    </div>
    <button class="save-img" @click="saveImg()" title="单击生成图片"></button>
    <div class="pop-img" id="pop" v-show="isShow" @click="showHide()">
    </div>
  </div>
</template>

<script>
  import Top from '../components/Header'
  import OrderEdit from '../components/OrderEdit'
  import OrderView from '../components/OrderView'
  import Under from '../components/Footer'
  import html2canvas from 'html2canvas'
  import canvas2image from '../utils/canvas2image'
  import *as common from "../utils/common.js"

  export default {
    name: "Home",
    data() {
      return {
        dates: [
          {
            title: "",
            content: [
              {
                time: "",
                meeting: "",
                isTitle: false,
                isUnderLine: false
              },
            ]
          }
        ],
        colors: {
          dateBgColor: "#008fd3",
          dateColor: "#ffffff",
          timeColor: "#ffffff",
          meetColor: "#ffffff",
          contentBgColor: "#001c3c",
          lineColor: "#008fd3"
        },
        isShow: false,
        templateName: "",
      }
    },
    created() {
      this.templateName = this.$route.params.templateName;
    },
    mounted() {
      /**
       * 通过野狗云存储数据
       */
      const config = {
        syncURL: common.syncURL //本地测试版
        // syncURL: common.syncURL //线上打包正式版
      };
      wilddog.initializeApp(config);
      const ref = wilddog.sync().ref();
      ref.on("value", snapshot => {
        let data = snapshot.val()[this.templateName];

        if (data) {
          let dates = data.dates;
          let colors = data.colors;
          this.dates = dates;
          this.colors = colors;

        } else {
          const temp = wilddog.sync().ref(`${this.templateName}`);
          let obj = {};
          obj.dates = this.dates;
          obj.colors = this.colors;
          temp.set(obj)

        }
      })


    },
    components: {
      Top,
      OrderEdit,
      OrderView,
      Under
    },
    methods: {
      saveImg() {
        this.isShow = true;
        let viewHeight = document.getElementById('view').offsetHeight;
        let viewWidth = document.getElementById('view').offsetWidth;
        let editHeight = document.querySelector(".edit").offsetHeight;
        let pop = document.getElementById("pop");

        var canvas = document.createElement("canvas");
        var scale = 2;

        canvas.width = viewWidth * scale;
        canvas.height = viewHeight * scale;
        canvas.getContext("2d").scale(scale, scale);

        var opts = {
          scale: scale,
          canvas: canvas,
          logging: true,
          width: viewWidth,
          height: viewHeight
        };


        pop.style.height = viewHeight > editHeight ? viewHeight + "px" : editHeight + "px";

        html2canvas(document.getElementById('view'), opts).then(function (canvas) {
          let ctx = canvas.getContext('2d');

          ctx.imageSmoothingEnabled = false;
          let cvs = canvas2image.convertToImage(canvas, canvas.width, canvas.height, 'jpeg');

          pop.appendChild(cvs);
          cvs.style.margin = "0 auto";
          cvs.style.width = viewWidth + "px";

        });
      },
      showHide() {
        let pop = document.getElementById("pop");
        let removeObj = pop.querySelector("img");
        this.isShow = false;
        removeObj.parentNode.removeChild(removeObj);
      }
    }
  }
  console.log("-------本服务有搜药技术部《皓云轩》工作室提供-------")
</script>
<style scoped lang="less">
  #home {
    width: 1260px;
    margin: 0 auto;
    .edit {
      width: 500px;
      float: left;
      margin-right: 10px;
    }
    .edit-top-box {
      width: 500px;
      height: 300px;
      background: skyblue;
    }
    .view {
      width: 750px;
      float: left;
    }
    &:after {
      content: "";
      display: block;
      height: 0;
      clear: both;
      visibility: hidden;
    }
    .save-img {
      position: fixed;
      display: block;
      bottom: 100px;
      right: 0;
      width: 80px;
      height: 80px;
      background: url("../assets/save.png") no-repeat center;
      background-size: 50px 50px;
      border-radius: 50%;
    }
    .pop-img {
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      text-align: center;
      background: rgba(0, 0, 0, .8);
      z-index: 10;
    }
  }

</style>



