<template>
  <div id="login">
    <Modal v-model="create" width="500" :mask-closable="false" :closable="false">
      <p slot="header" style="color: #2d8cf0;text-align:center">
        <Icon type="information-circled"></Icon>
        <span>请选择或创建模板</span>
      </p>
      <div style="text-align:center">
        <ul>
          <li v-for="(item, key) in list" style="margin-bottom: 10px; position:relative;">
            <div v-for="(val, k) in item">
              <Button long @click="selectTemplate(k)">{{val}}</Button>
              <div style="position:absolute;top:0; right:0;">
                <Button v-if="showDelete" type="warning" @click="deleteOk(k)">删除</Button>
              </div>
            </div>

          </li>
          <li style="clear: both;"></li>
        </ul>
      </div>
      <div slot="footer">
        <Button type="primary" size="large" @click="crateTemplate">创建模板</Button>
        <Button @click="deleteTemplate">删除模板</Button>
      </div>
    </Modal>
    <Modal
      v-model="createNew"
      width="400"
      :closable="false"
      :mask-closable="false"
      @on-ok="createOk"
    >
      <div slot="header" style="color: #2d8cf0;text-align:center">
        <span>请输入模板名称</span>
      </div>
      <div style="text-align: center">
        <Input v-model="newTemplateName" placeholder="请输入模板名称..." style="width: 300px"></Input>
      </div>
    </Modal>
  </div>
</template>
<script>
  import * as common from '../utils/common.js';
  import AV from 'leancloud-storage'
  import {mapGetters} from 'vuex';
  import Wilddog from 'wilddog'


  export default {
    name: "Login",
    data: () => ({
      create: true,
      createNew: false,
      list: [],
      newTemplateName: '',
      showDelete: false
//      urlParams: ''
    }),
    created() {
      let _this = this;
      const config = {
        syncURL: common.syncURL + "/list" //输入节点 URL
      };
      wilddog.initializeApp(config);
      const ref = wilddog.sync().ref();
      ref.on("value", snapshot => {
        if (snapshot.val()) {
          _this.list = snapshot.val();
          ref.off();
        } else {
          const config = {
            syncURL: common.syncURL //输入节点 URL
          };
          wilddog.initializeApp(config);
          ref.child('list').set([]);
        }
      });
    },
    mounted() {
    },
    watch: {},
    methods: {
      crateTemplate() {
        // 创建模板
        this.createNew = true;
      },
      selectTemplate(k) {
//        this.urlParams = k;
        this.$router.push({path: `/home/${k}`})

      },
      createOk() {
        // 确定创建模板

        if (this.newTemplateName == '') {
          this.$Message.info('模板名称不能为空');
        } else {
          // 随机生成字符串，用来获取模板数据
          let str = Math.random().toString(36).substr(2);
          let data = {};
          data[str] = this.newTemplateName;

          this.list.push(data);
          const config = {
            syncURL: common.syncURL
          };
          wilddog.initializeApp(config);
          const ref = wilddog.sync().ref();
          ref.child("list").set(this.list);
          ref.off();
        }
      },
      deleteTemplate() {
        this.showDelete = !this.showDelete;
      },
      deleteOk(k) {
        // 确定删除
        const config = {
          syncURL: common.syncURL
        };
        wilddog.initializeApp(config);
        const ref = wilddog.sync().ref();
        const LIST = wilddog.sync().ref(`/${k}`);

        let temp = this.list;
        let newList = []
        for (var i = 0; i < temp.length; i++) {
          var item = temp[i];
          if (!item[k]) {
            newList.push(item)
          }

        }
        this.list = newList;
        ref.child("list").set(this.list);

        LIST.remove();
      },
    }
  }
</script>
<style lang="less" scoped>

</style>



