<template>
  <div id="order-edit">
    <div v-for="(val,index) in dates">
      <h2 :style="{backgroundColor:colors.dateBgColor,color:colors.dateColor}">{{val.title}} </h2>
      <div class="time-wrap" :style="{backgroundColor:colors.contentBgColor}">
        <div class="time-content" v-for="(con,key) in val.content" :style="{borderBottomColor:colors.lineColor}"
             :class="[{title:con.isTitle},{underline:con.isUnderLine}]">
          <div :style="{color:colors.timeColor}">{{con.time}}</div>
          <p :style="{color:colors.meetColor}">{{con.meeting}}</p>
        </div>
      </div>
    </div>

  </div>
</template>
<script>
  export default {
    name: "OrderEdit",
    props: ['arthas'],
    data() {
      return {
        dates: [],
        colors: [],
      }
    },
    watch: {
      arthas: function (newVal, oldVal) {
        this.dates = newVal.dates;
        this.colors = newVal.colors;
      }
    },
    created() {
      this.dates = this.arthas;
    },
  }

</script>


<style scoped lang="less">
  #order-edit {
    h2 {
      font-size: 30px;
      background: #008fd3;
      color: #fff;
      padding-left: 20px;
      line-height: 2;
      font-weight: bold;
    }
    .time-wrap {
      background: #001c3c;
      color: #fff;
      .time-add-new {
        padding-left: 20px;
        button {
          margin: 10px auto;
          border-radius: 10px;
          padding: 5px 10px;
          font-size: 20px;
          background: #561874;
          color: #ff4;
        }
      }
    }
    .time-content {
      font-size: 24px;
      padding: 2px 10px 2px 20px;
      white-space: pre-wrap;
      div {
        float: left;
        width: 180px;
        min-height: 30px;
      }
      p {
        float: left;
        width: 522px;

      }
      &.title {
        font-size: 26px;
        padding-top: 10px;
        padding-bottom: 5px;
        font-weight: bold;
      }
      &.underline {
        padding-top: 10px;
        padding-bottom: 10px;
        border-bottom: 1px dashed #ccc;
      }
      &:after {
        content: "";
        display: block;
        height: 0;
        clear: both;
        visibility: hidden
      }
    }
  }
</style>



